/**
 * @name 
 * @params getList 
 * @params listData 
 * @params page 
 */
export default({getList="getList",listData="listData",page="page",initIndex="initIndex",modelData="modelData"})=> {
	var startPoint={}
	var endPoint={}
	return {
		data(){
			return {
				[listData]:[],
				[page]:1,
				[initIndex]:0,
				[modelData]:[
					// {label:"",backgroud:"color:#000000"},
					{label:"",backgroud:"color:#FF0000"},
					// {label:"",backgroud:"color:#FFB400"},
					// {label:"",backgroud:"color:#666666"},
				]
			}
		},
		onPullDownRefresh() {
			this.refreshes();
		},
		onReachBottom() {
			this[getList].call(this, this[page], this.__pulldone);
		},
		methods:{
			refreshes(){
				this[page]=1;
				this[getList].call(this, this[page], this.__pulldone);
			},
			__pulldone(data){
				var db = data || [];
				if (this[page] == 1) {
				  this[listData] = db;
				} else {
				  this[listData] = (this[listData] || []).concat(db);
				}
				uni.stopPullDownRefresh();
				this[page]++;
			},
			
			/**
			 * @name pulldown
			 * @params {boolean} isOpen
			 */
			setPullDown(isOpen){











			},
			
			/**
			 * @name 
			 */
			touchstart(e){
				startPoint={
					pageX:e.pageX||e.changedTouches[0].pageX,
					pageY:e.pageY||e.changedTouches[0].pageY,
				}
			},
			
			/**
			 * @name 
			 */
			touchend(e){
				endPoint={
					pageX:e.mp.changedTouches[0].pageX,
					pageY:e.mp.changedTouches[0].pageY,
				}
				//  10100
				var disX=endPoint.pageX-startPoint.pageX;//
				var disY=endPoint.pageY-startPoint.pageY;
				if(Math.abs(disX)>10||Math.abs(disY)>100){
					if(Math.abs(disX)>Math.abs(disY)){//
					    if(disX>10){
					        this.swiperight()//
					    };
					    if(disX<-10){
					        this.swipeleft()//
					    };
					}
				}
			},
			
			/**
			 * @name 
			 */
			swipeleft(){
				if(this[initIndex]<this[modelData].length-1)this[initIndex]++
				console.log("");
			},
			
			/**
			 * @name 
			 */
			swiperight(){
				if(this[initIndex]>0)this[initIndex]--
				console.log("");
			},
		}
	}
}