/**
 * @name 
 * @params getList 
 * @params listData 
 * @params page 
 */
import request from '../../api/request.js'
	import global from '../../static/js/global.js'
export default({getList="getList",listData="listData",page="page",initIndex="initIndex",modelData="modelData"})=> {
	var startPoint={}
	var endPoint={}
	return {
		data(){
			return {
				[listData]:[],
				[page]:1,
				[initIndex]:0,
				[modelData]:[
					
				]
			}
		},
		onPullDownRefresh() {
			this.refreshes();
		},
		onReachBottom() {
			this[getList].call(this, this[page], this.__pulldone);
		},
		methods:{
			refreshes(){
				this[page]=1;
				this[getList].call(this, this[page], this.__pulldone);
			},
			__pulldone(data){
				var db = data || [];
				if (this[page] == 1) {
				  this[listData] = db;
				} else {
				  this[listData] = (this[listData] || []).concat(db);
				}
				uni.stopPullDownRefresh();
				this[page]++;
			},
			
			/**
			 * @name pulldown
			 * @params {boolean} isOpen
			 */
			setPullDown(isOpen){











			},
			
			/**
			 * @name 
			 */
			touchstart(e){
				startPoint={
					pageX:e.pageX||e.changedTouches[0].pageX,
					pageY:e.pageY||e.changedTouches[0].pageY,
				}
			},
			
			/**
			 * @name 
			 */
			touchend(e){
				endPoint={
					pageX:e.mp.changedTouches[0].pageX,
					pageY:e.mp.changedTouches[0].pageY,
				}
				//  10100
				var disX=endPoint.pageX-startPoint.pageX;//
				var disY=endPoint.pageY-startPoint.pageY;
				if(Math.abs(disX)>10||Math.abs(disY)>100){
					if(Math.abs(disX)>Math.abs(disY)){//
					    if(disX>10){
					        this.swiperight()//
					    };
					    if(disX<-10){
					        this.swipeleft()//
					    };
					}
				}
			},
			
			/**
			 * @name 
			 */
			swipeleft(){
				if(this[initIndex]<this[modelData].length-1)this[initIndex]++
				console.log("");
			},
			
			/**
			 * @name 
			 */
			swiperight(){
				if(this[initIndex]>0)this[initIndex]--
				console.log("");
			},
			getDeviceType(){
				var that=this
				global.showLoading()
				var param = {
					openId:'wx123456789',
				}
				request.apiGet('/toc/deviceType/list',param).then((res) =>{
					if(res.code == '0'){
						res.data.forEach((item,index) =>{
							that[modelData].push({
									label:item.typeName,
									background:'FF0000',
									typeId:item.typeId
							})
						})
						global.hideLoading()
					}else{
						global.hideLoading()
						global.showToast(res.msg)
					}
				}).catch((reason) =>{
					global.hideLoading()
					global.showToast(reason)
				})
			}
		},
		mounted(){
			this.getDeviceType()
		}
	}
}