
const baseURL = 'http://47.103.152.26/api';
//  url:  param  way res
 /**
  *  @name 
  */
 async function apiPost(url,params){
 	if(!params)console.warn("params");
 	return await new Promise((resolve,reject)=>{
 		uni.request({
 			url:baseURL+url,
 			method :"POST",
 			header:{
 				// 'Token': token,
 				'Accept': 'application/json',
 				"content-type":"application/x-www-form-urlencoded"
 			},
 			data:params,
 			success(res) {
 				resolve(res.data)
 			},
 			fail(err) {
 				reject(err)
 			}
 		})
 	})
 }
 
 async function apiGet(url,params){
 	if(!params)console.warn("params");
 	return await new Promise((resolve,reject)=>{
 		uni.request({
 			url:baseURL+url,
 			method :"GET",
 			header:{
 				'Accept': 'application/json',
 				"content-type":"application/x-www-form-urlencoded"
 			},
 			timeout:6000,
 			data:params,
 			success(res) {
 				resolve(res.data)
 			},
 			fail(err) {
 				reject(err)
 			}
 		})
 	})
 }
 
 
 
