 async function login(params){
 	return await new Promise((resolve,reject)=>{
 		uni.request({
 			url:baseURL+'/login',
 			method :"GET",
 			header:{
 				// 'Token': token,
 				'Accept': 'application/json',
 				"content-type":"application/x-www-form-urlencoded"
 			},
 			data:params,
 			success(res) {
 				resolve(res.data)
 			},
 			fail(err) {
 				reject(err)
 			}
 		})
 	})
 }

export default {
    baseURL,
	apiPost,
	apiGet,
	login
}